export const selectIsLoggedIn = state => state.auth.isLoggedIn; //   isLoggedIn   auth
export const selectUser = state => state.auth.user; //   user   auth
export const selectIsRefreshing = state => state.auth.isRefreshing; //   isRefreshing   auth
export const selectError = state => state.auth.error; //   error   auth
export const selectIsLoading = state => state.auth.isLoading; //   isLoading   auth
